    vertical-align: bottom; }
    .tlbx-table thead th .tblx-muted {
      line-height: 1.1em;
      display: inline-block; }
  .tlbx-table th,
  .tlbx-table td:not([scope]) {
    text-align: center;
    vertical-align: middle; }

.tlbx-contrast-color {
