      margin-bottom: 3rem; } }

.tlbx-sidebar-title {
  font-size: 1rem;
  margin: 0 1rem;
  margin-top: 0.5rem; }
  .tlbx-sidebar-title span {
    font-weight: 200; }

.tlbx-sidebar-version {
  margin: 0.5rem 1rem;
  margin-bottom: 1.5rem;
  font-size: 0.8rem;
  font-weight: 200; }



